import React from 'react';
import PropTypes from 'prop-types';
import { noop } from 'lodash';
import DSInput from '../../Input';
import { DateInputs } from './DateInputs';

export const DSDateInputV2 = ({
  onFocus = noop,
  onBlur = noop,
  onChange = noop,
  onKeyDown = noop,
  onError = noop,
  value = '',
  disabled = false,
  containerProps = {},
  tabIndex,
}) => (
  <DSInput
    containerProps={containerProps}
    customInputType={() => (
      <DateInputs
        disabled={disabled}
        onBlur={onBlur}
        onChange={onChange}
        onCustomKeyDown={onKeyDown}
        onError={onError}
        time={value}
        tabIndex={tabIndex}
      />
    )}
    disableTooltip={false}
    disabled={disabled}
    onBlur={onBlur}
    onFocus={onFocus}
    onKeyDown={onKeyDown}
    value={value}
    tabIndex={tabIndex}
  />
);

DSDateInputV2.propTypes = {
  containerProps: PropTypes.shape({}),
  /**
   * Triggers the onBlur when focus moves out of date input
   */
  onBlur: PropTypes.func,
  /**
   * Triggers the onFocus when the component gets the focus
   */
  onFocus: PropTypes.func,
  /**
   * Triggers the onKeyDown
   */
  onKeyDown: PropTypes.func,
  /**
   * Allows a function that is triggered once the date input changes
   */
  onChange: PropTypes.func,
  /**
   * Triggers the onError once one input date is wrong
   */
  onError: PropTypes.func,
  /**
   * Default value once the component is initialized MMDDYYYY
   */
  value: PropTypes.string,
  /**
   * Whether the date input is disabled or not
   */
  disabled: PropTypes.bool,
  /**
   * tab index
   */
  tabIndex: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
};
