/* eslint-disable max-lines */
import React from 'react';
import { PropTypes, describe } from 'react-desc';
import 'react-dates/initialize';
import { SingleDatePickerPhrases } from 'react-dates';
import { convertPropToCssClassName } from '@elliemae/ds-classnames';
import moment from 'moment';
import {
  DatePickerNavigation,
  DatePickerDay,
  DatePickerPicker,
  DatePickerDropdown,
  DatePickerSingleDate,
  DatePickerHeader,
  DatePickerRange,
  DatePickerRangeHeader,
  SingleRangeDateSwitcher,
  renderMonthElement,
} from './components';
import DSDatePickerImpl from './components/DSDatePickerImpl';
import DSDatePickerController from './components/DatePickerController/DatePickerController';
import {
  PickerPropsTypes,
  Picker,
} from './components/DatePickerPicker/DatePickerPicker';

const DSDatePicker = ({
  containerProps = {},
  id = '',
  appendToBody = true,
  className = '',
  date,
  value,
  placeholder = '',
  disabled = false,
  required = false,
  readOnly = false,
  hasError = false,
  screenReaderInputMessage = 'Select date',
  enableOutsideDays = true,
  initialVisibleMonth = moment(),
  firstDayOfWeek = 0,
  keepOpenOnDateSelect = false,
  hideKeyboardShortcutsPanel = true,
  onChange = () => null,
  onDateChange = undefined,
  onPrevMonthClick = () => null,
  onNextMonthClick = () => null,
  onClose = () => null,
  onFocus = () => null,
  onBlur = () => null,
  phrases = SingleDatePickerPhrases,
  displayFormat = 'L',
  displayFormatDay = 'D',
  transitionDuration = 0,
  verticalSpacing = 0,
  isDayBlocked = () => false,
  isOutsideRange = () => false,
  isDayHighlighted = () => false,
  numberOfMonths = 1,
  openDirection = 'down',
  tabIndex = '',
  zIndex = 'auto',
  pickerProps = {},
  calendarProps = {},
}) => {
  const { cssClassName } = convertPropToCssClassName('datepicker', className, {
    hasError,
    readOnly,
    disabled,
    id,
  });
  const safeDate = date || value;
  return (
    <div className={cssClassName} {...containerProps}>
      <DSDatePickerImpl
        appendToBody={appendToBody}
        date={safeDate}
        disabled={disabled}
        displayFormat={displayFormat}
        displayFormatDay={displayFormatDay}
        enableOutsideDays={enableOutsideDays}
        firstDayOfWeek={firstDayOfWeek}
        hideKeyboardShortcutsPanel={hideKeyboardShortcutsPanel}
        initialVisibleMonth={() =>
          initialVisibleMonth ? moment(initialVisibleMonth) : moment(safeDate)
        }
        isDayBlocked={isDayBlocked}
        isDayHighlighted={isDayHighlighted}
        isOutsideRange={
          isOutsideRange === true || isOutsideRange === false
            ? () => isOutsideRange
            : isOutsideRange
        }
        keepOpenOnDateSelect={keepOpenOnDateSelect}
        numberOfMonths={numberOfMonths}
        onBlur={onBlur}
        onClose={onClose}
        onDateChange={(...args) => {
          if (onDateChange) onDateChange(...args);
          if (onChange) onChange(...args);
        }}
        onFocus={onFocus}
        onNextMonthClick={onNextMonthClick}
        onPrevMonthClick={onPrevMonthClick}
        openDirection={openDirection}
        phrases={phrases}
        placeholder={placeholder}
        readOnly={readOnly}
        required={required}
        screenReaderInputMessage={screenReaderInputMessage}
        transitionDuration={transitionDuration}
        value={safeDate}
        verticalSpacing={verticalSpacing}
        tabIndex={tabIndex}
        zIndex={zIndex}
        pickerProps={pickerProps}
        calendarProps={calendarProps}
      />
    </div>
  );
};

const datePickerProps = {
  containerProps: PropTypes.shape({}).description(
    'Set of Properties attached to the main container',
  ),
  id: PropTypes.string.description('components id'),
  appendToBody: PropTypes.bool
    .description('append component to body or not')
    .defaultValue(true),
  className: PropTypes.string.description('html class attribute'),
  date: PropTypes.instanceOf(Date, moment)
    .description('selected date')
    .defaultValue(undefined),
  value: PropTypes.instanceOf(Date, moment).description('value'),
  placeholder: PropTypes.string.description('input placeholder value'),
  disabled: PropTypes.bool
    .description('disable component or not')
    .defaultValue(false),
  required: PropTypes.bool
    .description('value is required or not')
    .defaultValue(false),
  readOnly: PropTypes.bool
    .description('is read only or not')
    .defaultValue(false),
  hasError: PropTypes.bool.description('set error state').defaultValue(false),
  screenReaderInputMessage: PropTypes.string
    .description('message to anounce by screen reader')
    .defaultValue('select date'),
  enableOutsideDays: PropTypes.bool
    .description('enable days oustide month or not')
    .defaultValue(true),
  initialVisibleMonth: PropTypes.instanceOf(Date, moment).description(
    'initial month to display',
  ),
  firstDayOfWeek: PropTypes.number
    .description('first day of week')
    .defaultValue(0),
  keepOpenOnDateSelect: PropTypes.bool
    .description('keep picker open when a date is selected')
    .defaultValue(false),
  hideKeyboardShortcutsPanel: PropTypes.bool
    .description('hide keyabord shortcut panel or not')
    .defaultValue(true),
  onChange: PropTypes.func.description('function executed when date changes'),
  onDateChange: PropTypes.func.description(
    'function executed when date changes',
  ),
  onPrevMonthClick: PropTypes.func.description(
    'function executed when clicking on prev month button',
  ),
  onNextMonthClick: PropTypes.func.description(
    'function executed when clicking on next month button',
  ),
  onBlur: PropTypes.func.description(
    'function executed when component loses focus',
  ),
  onClose: PropTypes.func.description(
    'function executed when component closes',
  ),
  onFocus: PropTypes.func.description(
    'function executed when component gains focus',
  ),
  phrases: PropTypes.shape({
    calendarLabel: PropTypes.string,
    closeDatePicker: PropTypes.string,
    clearDates: PropTypes.string,
    focusStartDate: PropTypes.string,
    jumpToPrevMonth: PropTypes.string,
    jumpToNextMonth: PropTypes.string,
    keyboardShortcuts: PropTypes.string,
    showKeyboardShortcutsPanel: PropTypes.string,
    hideKeyboardShortcutsPanel: PropTypes.string,
    openThisPanel: PropTypes.string,
    enterKey: PropTypes.string,
    leftArrowRightArrow: PropTypes.string,
    upArrowDownArrow: PropTypes.string,
    pageUpPageDown: PropTypes.string,
    homeEnd: PropTypes.string,
    escape: PropTypes.string,
    questionMark: PropTypes.string,
    selectFocusedDate: PropTypes.string,
    moveFocusByOneDay: PropTypes.string,
    moveFocusByOneWeek: PropTypes.string,
    moveFocusByOneMonth: PropTypes.string,
    moveFocustoStartAndEndOfWeek: PropTypes.string,
    returnFocusToInput: PropTypes.string,
    keyboardNavigationInstructions: PropTypes.string,
    chooseAvailableStartDate: PropTypes.func,
    chooseAvailableEndDate: PropTypes.func,
    dateIsUnavailable: PropTypes.func,
    dateIsSelected: PropTypes.func,
  })
    .description('react-dates phrases')
    .defaultValue(SingleDatePickerPhrases),
  displayFormat: PropTypes.string
    .description('display format')
    .defaultValue('L'),
  displayFormatDay: PropTypes.string
    .description('display format day')
    .defaultValue('D'),
  transitionDuration: PropTypes.number
    .description('transition duration')
    .defaultValue(0),
  verticalSpacing: PropTypes.number.description('vertical spacing'),
  isDayBlocked: PropTypes.func.description(
    'function that returns each day and disables desired dates',
  ),
  isOutsideRange: PropTypes.func.description(
    'function that returns each day and disables outisde range dates',
  ),
  isDayHighlighted: PropTypes.func.description(
    'function that returns each day and higlights desired dates',
  ),
  numberOfMonths: PropTypes.number.description('amount of months'),
  openDirection: PropTypes.string
    .description('open direction of the popup')
    .defaultValue('down'),
  tabIndex: PropTypes.string.description('overrides tab index of the element'),
  zIndex: PropTypes.string.description('overrides z-index of the element'),
  pickerProps: PropTypes.shape(PickerPropsTypes).description(
    'props to be passed to the picker container',
  ),
  calendarProps: PropTypes.shape({ tabIndex: PropTypes.number }).description(
    'props to be passes to the calendar container',
  ),
};

DSDatePicker.propTypes = datePickerProps;

const DatePickerWithSchema = describe(DSDatePicker);
DatePickerWithSchema.propTypes = datePickerProps;

export {
  DatePickerNavigation,
  DatePickerDay,
  DatePickerPicker,
  DSDatePickerController,
  DatePickerDropdown,
  DatePickerSingleDate,
  DatePickerHeader,
  DatePickerRange,
  DatePickerRangeHeader,
  SingleRangeDateSwitcher,
  Picker,
  renderMonthElement,
  DatePickerWithSchema,
};

export default DSDatePicker;
